.button {
  width: 246px;
  height: 48px;

  border-radius: 12px;
  background: #7ac100;

  color: #fff;
  text-align: center;
  font-variant-numeric: lining-nums proportional-nums;

  /* Pres → Caption S */
  font-family: StratosSkyeng;
  font-size: 20px;
  font-style: normal;
  font-weight: 400;
  line-height: 32px;
  /* 133.333% */

  border: none;

  cursor: pointer;
  margin-bottom: 10px;
  margin-top: 10px;
}

.button:hover {
  background: #7ac100cc;
}
